import React from 'react';
import './Footer.css';

class Footer extends React.Component {

  openPage(e) {
    let getId = e.currentTarget.id;
    console.log(getId);

    switch(getId) {
      case "twitter":
          window.open("https://twitter.com/KylePolson2", '_blank');
          window.open("https://twitter.com/KylePolson2");
          break;
      case "codepen":
          window.open("https://codepen.io/kzack/", '_blank');
          window.open("https://codepen.io/kzack/");
          break;
      case "github":
          window.open("https://github.com/kzack123", '_blank');
          window.open("https://github.com/kzack123");
          break;
      case "linkedin":
          window.open("https://www.linkedin.com/in/kyle-polson-a90461168/", '_blank');
          window.open("https://www.linkedin.com/in/kyle-polson-a90461168/");
          break;
    }
  }

  render() {
    const comma = '\'';
    return (
      <div className="contact">
        <div className="container">
          <div className="contactMe">
            <div className="me">
              <h1>Kyle Polson</h1>
              <p>Web Developer, coder, learning new and exciting things every day.
              Hope you enjoyed learning about me. Here are ways you can get in touch!</p>
            </div>
            <div className="tabs">
              <div className="email">
                <h2>Contact Me at</h2>
                <p>KylePolson1993@gmail.com</p>
              </div>
              <div className="socialMedia">
                <h2>Social Media</h2>
                <p id="twitter" onClick={this.openPage}>Twitter</p>
                <p id="codepen" onClick={this.openPage}>CodePen</p>
                <p id="github" onClick={this.openPage}>Github</p>
                <p id="linkedin" onClick={this.openPage}>LinkedIn</p>
              </div>
            </div>
            <div className="copyright">
              <p><i>Copyright Kyle Polson &copy;</i></p>
            </div>
          </div>
          <p className="coded"><i>Coded by Kyle Polson</i></p>
        </div>
      </div>
    );
  }
}


export default Footer;



