import React from 'react';
import './Header.css';

import profile from './done.png';
import drawing from "./desk_final.png";


let i = 0;

class Header extends React.Component {
  constructor(props) {
    super(props);

    this.typeWriter = this.typeWriter.bind(this);
  }

  typeWriter() {
    let txt = "Front-end Web Developer";
    let k = 0;

    if (i < txt.length) {
      console.log(i);
      document.getElementById("code").innerHTML += txt.charAt(i);
      i++;
      setTimeout(this.typeWriter, 150);
    }

}

  componentDidMount() {
    console.log("yes");
    this.typeWriter();
  }

  render() {
    return  (
      <div className="header">
        <div className="heading">
          <h1 id="code"></h1>
          <h2></h2>
          <div className="picDiv">
          <img id="profile" src={profile}/>
          </div>
          <img id="drawing" src={drawing}/>
        </div>
      </div>
    );
  }
}


export default Header;



