import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import Header from './Components/Header/Header.js';
import NavBar from './Components/NavBar/NavBar.js';
import About from './Components/About/About.js';
import Languages from './Components/Languages/Languages.js';
import Projects from './Components/Projects/Projects.js';
import Footer from './Components/Footer/Footer.js';

import { Link, DirectLink, Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      style: 'normStyle',
      projectTab: 'aboutPageHidden',
    }
    this.changeNav = this.changeNav.bind(this);
    this.enterTab = this.enterTab.bind(this);
    this.leaveTab = this.leaveTab.bind(this);
  }

  changeNav() {
    if (this.state.style === "normStyle") {
      if (window.pageYOffset >= 601) {
        this.setState({style: "lockStyle"});
      }
    } else {
      if (this.state.style === "lockStyle"){
        if (window.pageYOffset <= 699)
        this.setState({style: "normStyle"});
      }
    }
  }

  enterTab(el) {
    console.log(el);
      this.setState({projectTab: 'aboutPage'});
  }

  leaveTab() {
    this.setState({projectTab: 'aboutPageHidden'});
  }

  render() {
    return (
      <div  className="App">
        <Header name="home"  />
        <NavBar findStyle={this.state.style} />
        <About name="test1" />
        <Languages name="languages" />
        <Projects className="test" name="test" findStyle={this.state.projectTab} enterTab={this.enterTab} leaveTab={this.leaveTab}/>
        <Footer className="footer" name="footer" />

      </div>
    );
  }
}



export default App;



