import React from 'react';
import './NavBar.css';
import logo from './dev.png';

import { Link, DirectLink, Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll';

class NavBar extends React.Component {

  render() {
    return (
      <div  onClick={this.props.click} className={this.props.findStyle}>

        <div className="navTabs">
        <h3><Link activeClass="active" offset={-400} to="header" spy={true} smooth={'easeOutQuad'} duration={500} >Home</Link></h3>
        <h3><Link activeClass="active" offset={-30} to="about" spy={true} smooth={true} duration={500} >About</Link></h3>
        <h3><Link activeClass="active" offset={-20} to="languages" spy={true} smooth={true} duration={500} >Experience</Link></h3>
        <h3><Link activeClass="active" offset={-200} to="project" spy={true} smooth={true} duration={500} >Projects</Link></h3>
        <h3><Link activeClass="active" offset={-166} to="contact" spy={true} smooth={'easeOutQuad'} duration={800} >Contact</Link></h3>
        </div>
      </div>
    );
  }
}


export default NavBar;



