import React from 'react';
import './Languages.css';

import html from './logos/html.svg';
import css from './logos/css.svg';
import bootstrap from './logos/bootstrap.svg';
import javascript from './logos/javascript.svg';
import react from './logos/react.svg';
import jquery from './logos/jquery.svg';
import coding from './logos/coder.png';
import programmer from './logos/programmer.png';
import book from './logos/open-book.png';



class Languages extends React.Component {
  render() {
    let colors = {
      color: "blue"
    };
    return (
      <div className="languages">
        <div className="logos">
          <img className="pic" src={html}/>
          <img className="pic" src={css}/>
          <img className="pic" src={bootstrap}/>
        </div>
        <div className="experience">
          <img id="coder" src={coding}/>
          <h2>Experience</h2>
          <div className="topLogo">
            <img id="learning" src={book}/>
            <img id="programmer" src={programmer}/>
          </div>
          <div className="schools">
            <div className="studied">
              <h2>Studied at</h2>
              <div>
                <p>Renton Technical College</p>
                <p>Codecademy</p>
                <p>FreeCodeCamp</p>
                <p>GitHub</p>
                <p>Stackoverflow</p>
                <p>Udemy</p>
              </div>
            </div>
            <div className="studied">
              <h2>Proficient in</h2>
              <div>
                <p>HTML / CSS / BootStrap</p>
                <p>React</p>
                <p>SQL</p>
                <p>Redux</p>
                <p>jQuery</p>
                <p>Javascript</p>
              </div>
            </div>
          </div>
        </div>
        <div className="logos">
          <img className="pic" src={javascript}/>
          <img className="pic" src={react}/>
          <img className="pic" src={jquery}/>
        </div>
      </div>
    );
  }
}

export default Languages;



