import React from 'react';
import './About.css';


class About extends React.Component {
  render() {
    const comma = '\'';
    return (
      <div className="info">
        <div className="about">
          <h3>Hello... My name is Kyle,</h3>
          <p>And I have a particular set of skills, skills
          I have acquired over a very long career. Skills that make me proficient
          in several languages. If you hire me now, that'll be the best decision you've
          made. But if you don{comma}t, I will look for you, I will find you, and I will email
          you for a follow up.</p>
        </div>
      </div>
    );
  }
}


export default About;



