import React from 'react';
import './Projects.css';


import main from './images/jammming.png';
import quiz from './images/quiz.png';
import ravanes from './images/ravanes.png';
import blocks from './images/blocks.png';
import jammming from './images/login.png';
import projects from './images/web.png';


class Projects extends React.Component {
  constructor(props) {
    super(props);
    this.show = this.show.bind(this);
    this.leave = this.leave.bind(this);
  }
  show(e) {
    let getId = e.currentTarget.id;
    let findTab = document.getElementById(getId+1);
    let findFilter = document.getElementById(getId+2);

    findTab.className = "aboutPage";
    findFilter.className = "makeLight";
  }

  leave(e) {
    let getId = e.currentTarget.id;
    let findTab = document.getElementById(getId+1);
    let findFilter = document.getElementById(getId+2);

    findTab.className = "aboutPageHidden";
    findFilter.className = "makeNormal";

  }

  openPage(e) {
    let getId = e.currentTarget.id;
    console.log(getId);

    switch(getId) {
      case "jammming":
          window.open("https://kzack123.github.io/React-Login/", '_blank');
          window.open("https://kzack123.github.io/React-Login/");
          break;
      case "quiz":
          window.open("https://codepen.io/kzack/full/wpdQov", '_blank');
          window.open("https://codepen.io/kzack/full/wpdQov");
          break;
      case "ravanes":
          window.open("http://ko-ravenous.surge.sh", '_blank');
          window.open("http://ko-ravenous.surge.sh");
          break;
      case "blocks":
          window.open("https://kzack123.github.io/Blocks/", '_blank');
          window.open("https://kzack123.github.io/Blocks/");
          break;
      case "inDev":
          window.open("http://ko-Jamming-prj.surge.sh", '_blank');
          window.open("http://ko-Jamming-prj.surge.sh");
          break;
    }

  }

  /*give each div with the classname of snapshot its own classname based on camera or eyes
  then seach for id with said div classname such as camera then the pop up with the id of
  camera or eye will be givin the classname of either this.state.hover or this.state.hidden */


  render() {
    return (
      <div className="projects">
        <div className="projectImg">
          <img src={projects}/>
        </div>
        <div className="project">
          <div onMouseEnter={this.show} id="jammming" onMouseLeave={this.leave} onClick={this.openPage} className="snapshot">
            <div id="jammming2" className="makeNormal">
              <img src={jammming}/>
            </div>
            <div className="centerPopup">
              <div id="jammming1" className="aboutPageHidden">
                <h2>Login</h2>
                <p><b>Developed with React, Javascript, and CSS. With this Webapp I built,
                   the user can create an account, and after the program checks all the inputs are 
                   filled in correctly, the user can log in with their information, and see 
                   the account they just created.</b></p>
              </div>
            </div>
          </div>
          <div onMouseEnter={this.show} id="quiz" onMouseLeave={this.leave} onClick={this.openPage} className="snapshot">
            <div id="quiz2" className="makeNormal">
              <img src={quiz}/>
            </div>
            <div className="centerPopup">
              <div id="quiz1" className="aboutPageHidden">
                <h2>Quized</h2>
                <p><b>Developed with HTML, CSS, Javascript and jQuery. With the creation of this project 
                  I wanted to master using jQuery, to create a page where the user can take a quiz, 
                  and review their score at the end.</b></p>
              </div>
            </div>
          </div>
        </div>
        <div className="project">
          <div onMouseEnter={this.show} id="ravanes" onMouseLeave={this.leave} onClick={this.openPage} className="snapshot">
            <div id="ravanes2" className="makeNormal">
              <img src={ravanes}/>
            </div>
            <div className="centerPopup">
              <div id="ravanes1" className="aboutPageHidden">
                <h2>Ravanes</h2>
                <p><b>Developed with React and Javascript. A Webapp I Developed so that the user can search 
                  for restaurants in a specific area using Yelps API as the viewable results.</b></p>
              </div>
            </div>
          </div>
          <div onMouseEnter={this.show} id="blocks" onMouseLeave={this.leave} onClick={this.openPage} className="snapshot">
            <div id="blocks2" className="makeNormal">
              <img src={blocks}/>
            </div>
            <div className="centerPopup">
              <div id="blocks1" className="aboutPageHidden">
                <h2>Match</h2>
                <p><b>Developed with HTML, CSS, Bootstrap, Javascript and jQuery. A Web memorization game I 
                  made from scratch, for fun, to test your skills and memorizing to find where the matching block is.</b></p>
              </div>
            </div>
          </div>
        </div>
        <div className="project">
          <div onMouseEnter={this.show} id="inDev" onMouseLeave={this.leave} onClick={this.openPage} className="inDev">
            <div id="inDev2" className="makeNormal">
              <img src={main}/>
            </div>
            <div className="centerPopup">
              <div id="inDev1" className="aboutPageHidden">
                <h2>Jammming</h2>
                <p><b>Developed with React and Javascript. In this Webapp I created, the user can search for songs 
                  through Spotify's libreries using their API, and after finding a song they like, they are able to add the song to their own playlist.</b></p>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Projects;



